import AbstractView from '../framework/view/abstract-view.js';
import {SortTypes} from '../constants.js';

function createSortingTemplate(type) {
  const isChecked = (currentType) => currentType === type ? 'checked' : '';

  return (`<form class="trip-events__trip-sort  trip-sort" action="#" method="get">
            <div class="trip-sort__item  trip-sort__item--day">
              <input id="sort-day" class="trip-sort__input  visually-hidden" data-sort-type="${SortTypes.DAY}" ${isChecked(SortTypes.DAY)} type="radio" name="trip-sort" value="sort-day">
              <label class="trip-sort__btn" for="sort-day">Day</label>
            </div>

            <div class="trip-sort__item  trip-sort__item--event">
              <input id="sort-event" class="trip-sort__input  visually-hidden" type="radio" name="trip-sort" value="sort-event" disabled>
              <label class="trip-sort__btn" for="sort-event">Event</label>
            </div>

            <div class="trip-sort__item  trip-sort__item--time">
              <input id="sort-time" class="trip-sort__input  visually-hidden" data-sort-type="${SortTypes.TIME}" ${isChecked(SortTypes.TIME)} type="radio" name="trip-sort" value="sort-time">
              <label class="trip-sort__btn" for="sort-time">Time</label>
            </div>

            <div class="trip-sort__item  trip-sort__item--price">
              <input id="sort-price" class="trip-sort__input  visually-hidden" data-sort-type="${SortTypes.PRICE}" ${isChecked(SortTypes.PRICE)} type="radio" name="trip-sort" value="sort-price">
              <label class="trip-sort__btn" for="sort-price">Price</label>
            </div>

            <div class="trip-sort__item  trip-sort__item--offer">
              <input id="sort-offer" class="trip-sort__input  visually-hidden" type="radio" name="trip-sort" value="sort-offer" disabled>
              <label class="trip-sort__btn" for="sort-offer">Offers</label>
            </div>
          </form>`);
}

export default class Sorting extends AbstractView{
  #handleSortTypeChange = null;
  #currentSort = null;

  constructor({onSortTypeChange, currentSort}) {
    super();
    this.#handleSortTypeChange = onSortTypeChange;
    this.#currentSort = currentSort;

    this.element.addEventListener('click', this.#sortTypeChangeHandler);
  }

  get template() {
    return createSortingTemplate(this.#currentSort);
  }

  #sortTypeChangeHandler = (evt) => {
    if (!evt.target.hasAttribute('data-sort-type')) {
      return;
    }

    evt.preventDefault();
    this.#handleSortTypeChange(evt.target.dataset.sortType);
  };
}
